import axios from "axios";
import { useState, useEffect } from "react";
import { DataGrid } from "@mui/x-data-grid";

const UsersList = () => {
  const uri = "https://react-mongo-lloyd.herokuapp.com/api/";
  const [users, setUsers] = useState([]);

  useEffect(() => {
    const getUsers = async () => {
      const usersList = await axios.get(`${uri}users`);
      //   console.log(usersList.data.data);
      setUsers(usersList.data.data);
    };
    getUsers();
  }, []);

  const columns = [
    { field: "_id", headerName: "ID", width: 220 },
    { field: "name", headerName: "Name", width: 220 },
    { field: "email", headerName: "Email", width: 220 },
  ];
  return (
    <>
      <div style={{ height: 400, width: "100%", textAlign: "center" }}>
        <h3>Users</h3>
        <DataGrid rows={users} columns={columns} getRowId={(row) => row._id} />
      </div>
    </>
  );
};

export default UsersList;
