import axios from "axios";
import { useState, useEffect } from "react";
import { DataGrid } from "@mui/x-data-grid";

const TransactionsList = () => {
  const uri = "https://react-mongo-lloyd.herokuapp.com/api/";
  const [transactions, setTransactions] = useState([]);

  useEffect(() => {
    const getTransactions = async () => {
      const fruits = await axios.get(`${uri}items`);
      const transactionsList = await axios.get(`${uri}transactions`);
      console.log(transactionsList.data.data);
      //   console.log("fruits", fruits.data.data);
      setTransactions(transactionsList.data.data);
      getItems(transactionsList.data.data, fruits.data.data);
    };
    const getItems = (data, fruits) => {
      data.forEach((item, index) => {
        console.log(index, item.items);
      });

      fruits.forEach((item) => {
        console.log(item.item);
      });
    };
    getTransactions();
  }, []);
  const columns = [
    { field: "_id", headerName: "Transaction ID", width: 220 },
    { field: "userName", headerName: "Name", width: 220 },
    { field: "finalBill", headerName: "Bill", width: 130 },
  ];
  return (
    <>
      <div
        style={{
          height: 400,
          width: "70%",
          textAlign: "center",
          margin: "auto",
        }}
      >
        <h3>Transactions</h3>
        <DataGrid
          rows={transactions}
          columns={columns}
          getRowId={(row) => row._id}
          autoPageSize={true}
        />
      </div>
    </>
  );
};

export default TransactionsList;
