import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
} from "chart.js";

import { Bar } from "react-chartjs-2";
import { Doughnut } from "react-chartjs-2";

import axios from "axios";
import { useState, useEffect } from "react";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement
);
const DataCharts = () => {
  const uri = "https://react-mongo-lloyd.herokuapp.com/api/";

  const [barChartData, setBarChartData] = useState({
    labels: ["Placeholder"],
    datasets: [
      {
        label: "Placeholder",
        backgroundColor: "rgba(75,192,192,1)",
        borderColor: "rgba(0,0,0,1)",
        borderWidth: 1,
        data: [0],
      },
    ],
  });
  const [salesChartData, setSalesChartData] = useState({
    labels: ["Placeholder"],
    datasets: [
      {
        label: "Placeholder",
        backgroundColor: "rgba(75,192,192,1)",
        borderColor: "rgba(0,0,0,1)",
        borderWidth: 1,
        data: [0],
      },
    ],
  });
  const [doughnutData, setDoughnutData] = useState({
    labels: [],
    datasets: [
      {
        label: "# of Votes",
        data: [],
        backgroundColor: [
          "rgba(255, 99, 132, 0.2)",
          "rgba(54, 162, 235, 0.2)",
          "rgba(255, 206, 86, 0.2)",
          "rgba(75, 192, 192, 0.2)",
          "rgba(153, 102, 255, 0.2)",
          "rgba(255, 159, 64, 0.2)",
        ],
        borderColor: [
          "rgba(255, 99, 132, 1)",
          "rgba(54, 162, 235, 1)",
          "rgba(255, 206, 86, 1)",
          "rgba(75, 192, 192, 1)",
          "rgba(153, 102, 255, 1)",
          "rgba(255, 159, 64, 1)",
        ],
        borderWidth: 1,
      },
    ],
  });
  const [salesDoughnutData, setSalesDoughnutData] = useState({
    labels: [],
    datasets: [
      {
        label: "# of Votes",
        data: [],
        backgroundColor: [
          "rgba(255, 99, 132, 0.2)",
          "rgba(54, 162, 235, 0.2)",
          "rgba(255, 206, 86, 0.2)",
          "rgba(75, 192, 192, 0.2)",
          "rgba(153, 102, 255, 0.2)",
          "rgba(255, 159, 64, 0.2)",
        ],
        borderColor: [
          "rgba(255, 99, 132, 1)",
          "rgba(54, 162, 235, 1)",
          "rgba(255, 206, 86, 1)",
          "rgba(75, 192, 192, 1)",
          "rgba(153, 102, 255, 1)",
          "rgba(255, 159, 64, 1)",
        ],
        borderWidth: 1,
      },
    ],
  });

  useEffect(() => {
    const getTransactions = async () => {
      const fruitsTotalSales = await axios.get(
        `${uri}transactions/view/totalFruitSaleCount`
      );
      const fruitsCount = await axios.get(
        `${uri}transactions/view/transactionFruitCount`
      );

      getItems(fruitsCount.data.data, fruitsTotalSales.data.data);
    };
    const getItems = (fruitsCount, totalSales) => {
      const labels = [];
      const salesData = [];
      const countData = [];
      totalSales.forEach((fruit) => labels.push(fruit._id));
      totalSales.forEach((number) => salesData.push(number.totalSales));
      fruitsCount.forEach((fruit) => countData.push(fruit.count));
      setBarChartData({
        labels,
        datasets: [
          {
            label: "Total sales",
            backgroundColor: "rgba(75,192,192,1)",
            borderColor: "rgba(0,0,0,1)",
            borderWidth: 1,
            data: salesData,
          },
        ],
      });
      setDoughnutData({
        labels,
        datasets: [
          {
            label: "Total# of Sales",
            data: salesData,
            backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(255, 206, 86, 0.2)",
              "rgba(75, 192, 192, 0.2)",
              "rgba(153, 102, 255, 0.2)",
            ],
            borderColor: [
              "rgba(255, 99, 132, 1)",
              "rgba(54, 162, 235, 1)",
              "rgba(255, 206, 86, 1)",
              "rgba(75, 192, 192, 1)",
              "rgba(153, 102, 255, 1)",
            ],
            borderWidth: 1,
          },
        ],
      });
      setSalesChartData({
        labels,
        datasets: [
          {
            label: "Times bought",
            backgroundColor: "rgba(175,132,192,1)",
            borderColor: "rgba(0,0,0,1)",
            borderWidth: 2,
            data: countData,
          },
        ],
      });
      setSalesDoughnutData({
        labels,
        datasets: [
          {
            label: "Times Bought",
            data: countData,
            backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(255, 206, 86, 0.2)",
              "rgba(75, 192, 192, 0.2)",
              "rgba(153, 102, 255, 0.2)",
            ],
            borderColor: [
              "rgba(255, 99, 132, 1)",
              "rgba(54, 162, 235, 1)",
              "rgba(255, 206, 86, 1)",
              "rgba(75, 192, 192, 1)",
              "rgba(153, 102, 255, 1)",
            ],
            borderWidth: 1,
          },
        ],
      });
    };

    getTransactions();
  }, []);

  return (
    <>
      <div className="container">
        <div className="row">
          <div className="col-12 col-lg-6">
            <Bar
              data={barChartData}
              options={{
                plugins: {
                  legend: { position: "bottom" },
                  title: {
                    display: true,
                    text: "TOTAL SALES",
                    font: { size: 15 },
                  },
                },
              }}
            />
          </div>
          <div className="col-12 col-lg-6">
            <Bar
              data={salesChartData}
              options={{
                plugins: {
                  legend: { position: "bottom" },
                  title: {
                    display: true,
                    text: "Sample Title",
                    font: { size: 15 },
                  },
                },
              }}
            />
          </div>
        </div>
        <div className="row d-flex justify-content-around">
          <div className="col-6 col-lg-3">
            <Doughnut
              data={doughnutData}
              options={{
                plugins: {
                  legend: { position: "right" },
                  title: {
                    display: true,
                    text: "Total Sales of Fruits",
                    font: { size: 15 },
                  },
                },
              }}
            />
          </div>
          <div className="col-6 col-lg-3">
            <Doughnut
              data={salesDoughnutData}
              options={{
                plugins: {
                  legend: { position: "right" },
                  title: {
                    display: true,
                    text: "Sample Title",
                    font: { size: 15 },
                  },
                },
              }}
            />
          </div>
        </div>
      </div>
    </>
  );
};

export default DataCharts;
